"use client"

import GenericPage from "@/components/landing/GenericPage"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { useState } from "react"
import { CalendarIcon } from "lucide-react"

export default function RequestDemoPage() {
  const [isLoading, setIsLoading] = useState(false)
  const [isSuccess, setIsSuccess] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    const formData = new FormData(e.target as HTMLFormElement)
    const data = Object.fromEntries(formData.entries())

    try {
      const res = await fetch("/api/demo", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      })

      if (res.ok) {
        setIsSuccess(true)
      } else {
        alert("Something went wrong. Please try again.")
      }
    } catch (error) {
      console.error(error)
      alert("Error submitting form.")
    } finally {
      setIsLoading(false)
    }
  }

  if (isSuccess) {
    return (
      <GenericPage title="Permintaan Demo Terkirim">
        <div className="text-center py-20">
          <div className="w-16 h-16 bg-green-100 text-green-600 rounded-full flex items-center justify-center mx-auto mb-6">
            <CalendarIcon className="w-8 h-8" />
          </div>
          <h2 className="text-2xl font-bold mb-4">Terima Kasih!</h2>
          <p className="text-muted-foreground mb-8">
            Tim kami telah menerima permintaan demo Anda. Kami akan segera menghubungi Anda untuk konfirmasi jadwal dan detail pertemuan via Google Meet.
          </p>
          <Button onClick={() => setIsSuccess(false)} variant="outline">Kembali</Button>
        </div>
      </GenericPage>
    )
  }

  return (
    <GenericPage title="Request Demo">
      <div className="grid md:grid-cols-2 gap-12 items-start">
        <div>
          <h2 className="text-2xl font-bold mb-4">Lihat Bagaimana Sahabat Warga Bekerja</h2>
          <p className="text-muted-foreground mb-6">
            Jadwalkan sesi demo personal dengan tim kami untuk melihat langsung fitur-fitur unggulan yang dapat membantu pengelolaan lingkungan Anda.
          </p>
          <ul className="space-y-4 mb-8">
            <li className="flex items-start gap-3">
              <span className="w-6 h-6 rounded-full bg-primary/10 text-primary flex items-center justify-center text-sm font-bold">1</span>
              <div>
                <h4 className="font-semibold">Konsultasi Kebutuhan</h4>
                <p className="text-sm text-muted-foreground">Diskusikan tantangan spesifik di lingkungan RT/RW Anda.</p>
              </div>
            </li>
            <li className="flex items-start gap-3">
              <span className="w-6 h-6 rounded-full bg-primary/10 text-primary flex items-center justify-center text-sm font-bold">2</span>
              <div>
                <h4 className="font-semibold">Demo Produk Langsung</h4>
                <p className="text-sm text-muted-foreground">Tur lengkap fitur dashboard, aplikasi warga, dan sistem pembayaran.</p>
              </div>
            </li>
            <li className="flex items-start gap-3">
              <span className="w-6 h-6 rounded-full bg-primary/10 text-primary flex items-center justify-center text-sm font-bold">3</span>
              <div>
                <h4 className="font-semibold">Sesi Tanya Jawab</h4>
                <p className="text-sm text-muted-foreground">Tanyakan hal teknis, harga, atau implementasi.</p>
              </div>
            </li>
          </ul>
        </div>

        <div className="bg-muted/30 p-8 rounded-xl border">
          <form className="space-y-4" onSubmit={handleSubmit}>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">Nama Lengkap</label>
                <Input name="name" placeholder="Budi Santoso" required />
              </div>
              <div className="space-y-2">
                <label className="text-sm font-medium">No. WhatsApp</label>
                <Input name="phone" placeholder="0812..." required />
              </div>
            </div>
            
            <div className="space-y-2">
              <label className="text-sm font-medium">Email Kantor/Pribadi</label>
              <Input name="email" type="email" placeholder="nama@email.com" required />
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Nama Organisasi / Lingkungan</label>
              <Input name="company" placeholder="Contoh: RT 05 RW 03 Kel. Menteng" required />
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Pilih Jadwal Demo</label>
              <Input name="date" type="datetime-local" required />
              <p className="text-xs text-muted-foreground">Pilih waktu yang nyaman untuk Google Meet (durasi 45-60 menit).</p>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Catatan Tambahan (Opsional)</label>
              <Textarea name="message" placeholder="Apa yang ingin Anda fokuskan dalam demo?" />
            </div>

            <Button type="submit" className="w-full" size="lg" disabled={isLoading}>
              {isLoading ? "Mengirim Permintaan..." : "Jadwalkan Demo"}
            </Button>
            <p className="text-xs text-center text-muted-foreground mt-4">
              Dengan mendaftar, Anda menyetujui Kebijakan Privasi kami.
            </p>
          </form>
        </div>
      </div>
    </GenericPage>
  )
}
